<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="6">
                <h2>Создание пользователя</h2>
                <v-form>
                    <v-text-field
                            label="Email"
                            v-model="email"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-text-field
                            type="password"
                            label="Пароль"
                            v-model="password1"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-text-field
                            type="password"
                            label="Повторите пароль"
                            v-model="password2"
                            :rules="[v => !!v || 'Обязательное поле', v => v == password1 || 'Пароли должны совпадать']"
                            required
                    ></v-text-field>
                    <v-switch v-model="is_active" :label="is_active ? 'Активен' : 'Заблокирован'"></v-switch>
                    <v-switch v-model="is_admin" :label="is_admin ? 'Админ' : 'Клиент'"></v-switch>
                    <v-select
                            v-model="site"
                            :items="sites"
                            item-text="name"
                            item-value="id"
                            label="Сайт"
                    ></v-select>
                    <v-textarea
                            v-model="comment"
                            label="Комментарий"
                    ></v-textarea>
                    <v-btn @click="submit" :disabled="!email || !password1 || !password2 || (password1 != password2)" class="primary">
                        Создать пользователя
                    </v-btn>
                </v-form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex'

    export default {
        name: "AddAccount",
        data: () => ({
            email: null,
            password1: null,
            password2: null,
            is_active: true,
            is_admin: false,
            site: null,
            sites: null,
            comment: null
        }),
        methods: {
            ...mapActions(['setAlert']),
            async getSites() {
                const res = await this.axios({url: `${process.env.VUE_APP_SERVER_URL}api/v1/sites/`, method: 'GET'})
                this.sites = res.data
            },
            submit() {
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/users/`, method: 'POST', data: {
                        email: this.email,
                        password: this.password1,
                        password2: this.password2,
                        is_active: this.is_active,
                        is_staff: this.is_admin,
                        site: this.site,
                        comment: this.comment
                    }
                }).then(() => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'Пользователь создан'
                    })
                    this.$router.push('/accounts')
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            }
        },
        mounted() {
            this.getSites()
        }
    }
</script>

